<template>
  <div>
    <b-row>
      <b-col>
        <h3 class="mt-3 mb-3">
          1.
          <span>Datos del Titular</span>
        </h3>
      </b-col>
    </b-row>

    <b-row class="form-inputs-container">
      <b-col cols="12">
        <b-form-group label="Email *">
          <b-form-input v-model="email" placeholder="yo@ejemplo.com"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col xs="12" sm="6" md="6">
        <b-form-group label="Nombre *">
          <b-form-input v-model="name" placeholder="Su nombre"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col xs="12" sm="6" md="6">
        <b-form-group label="Télefono *">
          <b-form-input v-model="telephone" placeholder="Su número de teléfono"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "InformationGroup",
  data() {
    return {
      email: "",
      name: "",
      telephone: ""
    };
  },
  methods: {
    ...mapMutations(["changeDataForm"])
  },
  watch: {
    email(newData) {
      this.changeDataForm({ key: "email", value: newData });
    },
    name(newData) {
      this.changeDataForm({ key: "name", value: newData });
    },
    telephone(newData) {
      this.changeDataForm({ key: "telephone", value: newData });
    }
  }
};
</script>
