import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dataForm: {
      email: {
        value: "",
        required: true,
      },
      name: {
        value: "",
        required: true,
      },
      telephone: {
        value: "",
        required: false,
      },
      country: {
        value: "",
        required: false,
      },
      address: {
        value: "",
        required: false,
      },
      state: {
        value: "",
        required: false,
      },
      suburb: {
        value: "",
        required: false,
      },
      city: {
        value: "",
        required: false,
      },
      zip: {
        value: "",
        required: false,
      },
      conditions: {
        value: "not_accepted",
        required: true,
      },
    },
    statusErrors: false,
    errors: [],
  },
  mutations: {
    changeDataForm: (state, newData) => {
      state.dataForm[newData.key].value = newData.value;
    },
    changeErrors(state, data) {
      state.errors = data;
    },
    changeStatusErrors(state, data) {
      state.statusErrors = data;
    },
  },
  actions: {},
  modules: {},
});
