<template>
  <div>
    <b-row>
      <b-col>
        <h3 class="mt-3 mb-3">
          3.
          <span>Su Orden</span>
        </h3>
      </b-col>
    </b-row>

    <b-row>
      <b-col>
        <b-table-simple>
          <b-thead>
            <b-tr>
              <b-th>Producto</b-th>
              <b-th class="text-right">Subtotal</b-th>
            </b-tr>
          </b-thead>
          <b-tbody>
            <b-tr v-for="product in products" :key="product.name">
              <b-td>{{ product.name }}</b-td>
              <b-td class="text-right">US${{ product.subtotal }}</b-td>
            </b-tr>
          </b-tbody>
          <b-tfoot>
            <b-tr>
              <b-td>Total</b-td>
              <b-td class="text-right">US${{ total }}</b-td>
            </b-tr>
          </b-tfoot>
        </b-table-simple>
      </b-col>
    </b-row>

    <b-row class="mt-4">
      <b-container>
        <b-row class="payment-container justify-content-center">
          <b-col cols="6">
            <b-card
              @click="changeTypePayment('paypal')"
              :class="[classActive('paypal') ? 'border-danger' : 'hover-type-payment', 'text-center']"
              bg-variant="light"
            >
              <b-img
                v-bind="{
                  width: 75,
                  height: 75,
                }"
                src="assets/paypal.png"
                center
              ></b-img>
              <!-- <b-card-text class="mt-2">Paypal</b-card-text> -->
            </b-card>
          </b-col>

          <b-col cols="6">
            <b-card
              @click="changeTypePayment('stripe')"
              :class="[classActive('stripe') ? 'border-danger' : 'hover-type-payment', 'text-center']"
              bg-variant="light"
            >
              <b-img
                v-bind="{
                  width: 75,
                  height: 75,
                }"
                src="assets/stripe.svg"
                center
              ></b-img>
              <!-- <b-card-text class="mt-2">Stripe</b-card-text> -->
            </b-card>
          </b-col>
        </b-row>
      </b-container>
    </b-row>

    <b-row class="mt-5">
      <b-col>
        <b-button
          type="submit"
          variant="danger"
          size="lg"
          block
        >COMPLETAR ORDEN CON {{payment | upperCase}}</b-button>

        <b-form-group class="mt-3">
          <b-form-checkbox
            v-model="conditions"
            value="accepted"
            unchecked-value="not_accepted"
          >Entiendo la garantía de devolución de dinero de Gramupy</b-form-checkbox>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "Order",
  data() {
    return {
      payment: "paypal",
      total: "1,997.00",
      conditions: "not_accepted",
      products: [
        { name: "Curso de dominación de Instagram", subtotal: "1,997.00" }
      ]
    };
  },
  filters: {
    upperCase: function(value) {
      if (!value) return "";
      value = value.toString();
      return value.toUpperCase();
    }
  },
  methods: {
    ...mapMutations(["changeDataForm"]),
    classActive(type) {
      if (type === this.payment) {
        return true;
      }

      return false;
    },
    changeTypePayment(type) {
      this.payment = type;
    }
  },
  watch: {
    conditions(newData) {
      this.changeDataForm({ key: "conditions", value: newData });
    }
  }
};
</script>
