<template>
  <div>
    <b-row>
      <b-col>
        <h3 class="mt-3 mb-3">
          2.
          <span>Dirección de Envio</span>
        </h3>

        <p
          class="billing-description"
        >Coloque su dirección de envío a continuación para cualquier producto físico.</p>
      </b-col>
    </b-row>

    <b-row class="form-inputs-container">
      <b-col sm="12" md="6">
        <b-form-group label="País / región">
          <b-form-select v-model="country" :options="countries"></b-form-select>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="Estado">
          <b-form-select v-model="state" :options="states"></b-form-select>
        </b-form-group>
      </b-col>

      <b-col cols="12">
        <b-form-group label="Dirección">
          <b-form-input v-model="address" placeholder="Número de casa y nombre de la calle"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col cols="12">
        <b-form-group>
          <b-form-input v-model="suburb" placeholder="Barrio"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="Ciudad">
          <b-form-input v-model="city"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="Código Postal">
          <b-form-input v-model="zip"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "AdressGroup",
  data() {
    return {
      country: "",
      state: "",
      suburb: "",
      city: "",
      zip: "",
      address: "",
      countries: ["Spain", "Venezuela"],
      states: ["Alabama", "Alaska"]
    };
  },
  methods: {
    ...mapMutations(["changeDataForm"])
  },
  watch: {
    country(newData) {
      this.changeDataForm({ key: "country", value: newData });
    },
    state(newData) {
      this.changeDataForm({ key: "state", value: newData });
    },
    address(newData) {
      this.changeDataForm({ key: "address", value: newData });
    },
    suburb(newData) {
      this.changeDataForm({ key: "suburb", value: newData });
    },
    city(newData) {
      this.changeDataForm({ key: "city", value: newData });
    },
    zip(newData) {
      this.changeDataForm({ key: "zip", value: newData });
    }
  }
};
</script>
