<template>
  <div class="checkout">
    <b-card no-body>
      <template v-if="statusErrors" v-slot:header>
        <CardHeaderErrors />
      </template>

      <b-form @submit="onSubmit">
        <b-card-body class="px-2 px-sm-3 px-md-3 px-lg-5">
          <InformationGroup />

          <AddressGroup />
        </b-card-body>

        <p class="current-plan">
          <label>
            <input type="radio" name="pay_type" value="1" checked="checked" />
            <span class="product-title">
              <b>OFERTA ESPECIAL</b>: Haga 1x pago de US$1997 hoy.
            </span>
          </label>
        </p>

        <b-card-body class="px-2 px-sm-3 px-md-3 px-lg-5">
          <Order />
        </b-card-body>
      </b-form>
    </b-card>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

import { scroller } from "vue-scrollto/src/scrollTo";

import CardHeaderErrors from "./checkout/CardHeaderErrors";
import InformationGroup from "./checkout/InformationGroup";
import AddressGroup from "./checkout/AddressGroup";
import Order from "./checkout/Order";

export default {
  name: "Checkout",
  components: {
    CardHeaderErrors,
    InformationGroup,
    AddressGroup,
    Order
  },
  data() {
    return {
      errors: []
    };
  },
  computed: mapState(["statusErrors", "dataForm"]),
  methods: {
    ...mapMutations(["changeStatusErrors", "changeErrors"]),
    checkErrors() {
      let existErrors = false;

      for (const key of Object.keys(this.dataForm)) {
        if (
          key === "conditions" &&
          this.dataForm[key].value === "not_accepted"
        ) {
          this.errors.push({ key, message: `not accepted` });
          existErrors = true;
        } else if (
          this.dataForm[key].required === true &&
          this.dataForm[key].value.length < 1
        ) {
          this.errors.push({ key, message: `is a required field` });
          existErrors = true;
        }
      }

      if (existErrors) {
        this.changeErrors(this.errors);

        this.changeStatusErrors(true);

        const firstScrollTo = scroller();
        firstScrollTo("#top");
      }

      setTimeout(() => {
        this.errors = [];
        this.changeErrors();

        this.changeStatusErrors(false);
      }, 4000);
    },
    onSubmit(e) {
      e.preventDefault();
      this.checkErrors();
    }
  }
};
</script>
